html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-card {
  width: 1em;
  height: 1em;
  fill: #010101;
}

.icon-document {
  width: 1em;
  height: 1em;
  fill: #000;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-home {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-options {
  width: 1em;
  height: 1em;
  fill: #515151;
}

.icon-search {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-star {
  width: 1em;
  height: 1em;
  fill: #333;
}

.icon-tasks {
  width: 1em;
  height: 1em;
  fill: none;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.container {
  width: 1230px;
  margin: 0 auto;
}

.out {
  font-family: 'Roboto', sans-serif;
  font-size: 12px;
  font-weight: 400;
}

a {
  text-decoration: none;
}

.header__top {
  display: flex;
  align-items: center;
  padding: 20px 20px 15px 40px;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  padding-right: 20px;
  out-line-top: 1px solid #cccccc;
  out-line-bottom: 1px solid #cccccc;
}

.nav-list {
  display: flex;
  align-items: center;
}

.nav-list__item {
  display: flex;
  align-items: center;
}

.nav-list__item-icon {
  width: 12px;
  height: 12px;
  margin-right: 8px;
  margin-left: 25px;
}

.nav-list__item-icon .icon {
  fill: #cccccc;
}

.nav-list__link {
  text-transform: uppercase;
}

.is-active .nav-list__item-icon {
  display: none;
}

.is-active .nav-list__link {
  padding: 15px 30px;
  color: #ffffff;
}

.is-active.nav-list__item {
  background: black;
}

.search {
  display: flex;
  margin-right: 13px;
}

.search__input {
  width: 410px;
  height: 30px;
  border: 1px solid #cccccc;
}

.search__btn {
  width: 30px;
  height: 30px;
  padding: 0;
  margin: 0 auto;
  margin-left: -1px;
  font-size: 14px;
  line-height: 30px;
  background-color: transparent;
  border: 1px solid #cccccc;
}

.search__btn .icon-search {
  fill: #cccccc;
}

.options {
  display: flex;
  align-items: center;
  height: 30px;
  margin-right: 50px;
}

.options__icon {
  width: 12px;
  height: 12px;
  margin-right: 10px;
}

.user-block {
  display: flex;
  align-items: center;
}

.user-block__icon {
  width: 36px;
  height: 36px;
  padding: 11px;
  margin-right: 12px;
  background-color: #cccccc;
  border-radius: 50%;
  font-size: 14px;
}

.user-block__icon--small {
  width: 15px;
  height: 15px;
  padding: 0;
  margin-right: 6px;
  background-color: transparent;
  border-radius: none;
}

.user-block__email {
  margin-right: 12px;
}

.user-block__email .icon-home {
  fill: #878c8f;
}

.user-block__bg-jobs {
  margin-right: 12px;
  text-transform: uppercase;
}

.user-block__num {
  padding: 2px 4px;
  font-size: 10px;
  color: white;
  background-color: orange;
  border-radius: 50%;
}

.btn {
  font-size: 10px;
  color: #2e3236;
  padding: 6px 15px;
  background-color: transparent;
  border: 1px solid #cccccc;
  border-radius: 70px;
}

.logo {
  height: 35px;
  margin-right: 25px;
}
