.user-block {
	display: flex;
	align-items: center;

	&__icon {
		width: 36px;
		height: 36px;

		padding: 11px;
		margin-right: 12px;

		background-color: #cccccc;
		border-radius: 50%;

		font-size: 14px;

		&--small {
			width: 15px;
			height: 15px;

			padding: 0;
			margin-right: 6px;

			background-color: transparent;
			border-radius: none; } }


	&__email {
		margin-right: 12px;

		.icon-home {
			fill: #878c8f; } }

	&__bg-jobs {
		margin-right: 12px;
		text-transform: uppercase; }

	&__num {
		padding: 2px 4px;
		font-size: 10px;
		color: white;
		background-color: orange;
		border-radius: 50%; } }

