.search {
	display: flex;
	margin-right: 13px;

	&__input {
		width: 410px;
		height: 30px;

		border: 1px solid #cccccc; }

	&__btn {
		width: 30px;
		height: 30px;
		padding: 0;
		margin: 0 auto;
		margin-left: -1px;
		font-size: 14px;
		line-height: 30px;
		background-color: transparent;
		border: 1px solid #cccccc;

		.icon-search {
			fill: #cccccc; } } }

