.nav-list {

	display: flex;
	align-items: center;

	&__item {
		display: flex;
		align-items: center; }

	&__item-icon {
		width: 12px;
		height: 12px;

		margin-right: 8px;
		margin-left: 25px;

		.icon {
			fill: #cccccc; } }

	&__link {
		text-transform: uppercase; } }

.is-active .nav-list {

	&__item-icon {
		display: none; }

	&__link {
		padding: 15px 30px;
		color: #ffffff; } }

.is-active.nav-list__item {

	background: black; }

